.timeline-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
  width: 100%;
}

.timeline {
  width: calc(100% - 30rem);
}

.rct-sidebar {
  position: relative;
  padding-right: 0.25rem;
}

.rct-sidebar-resize {
  background-color: #ccc;
  position: absolute;
  top: 0;
  right: 0;
  width: 0.25rem;
  height: 100%;
  cursor: ew-resize;
  z-index: 100;
}

.right-side-items-container {
  display: flex;
  flex-direction: column;
  width: 23vw;
}

.action-buttons-container {
  display: flex;
  padding: 0 2rem 0 0;
  flex-direction: column;
  top: 0;
  right: 0;
}

.static-item:after {
  content: '';
  display: block;
  position: absolute;
  background: repeating-linear-gradient(
    -45deg,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0) 10px,
    #465298 10px,
    #465298 20px
  );
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: .3;
}

.milestone:hover .milestone-label {
  display: inline-block;
}

.milestone-label {
  display: none;
  width: 10rem;
  background-color: rgba(0, 0, 0, .7);
  position: absolute;
  top: 10%;
  left: 0.25rem;
  color: #fff;
  padding: 5px;
  font-family: "Roboto", sans-serif;
}

.milestone-date {
  font-size: 0.8rem;
}

.action-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.action-button {
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  background-color: #fff;
  border: 1px solid #333;
  color: #333;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  transition-duration: .5s;
  cursor: pointer;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                   supported by Chrome, Edge, Opera and Firefox */
}

.action-button.disabled {
  cursor: no-drop;
  background-color: #c3c3c3;
  color: #333;
}

.action-button:hover {
  background-color: #333;
  color: #fff;
}

.resource {
  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 2rem;
  position: relative;
}

.resource .edit-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1rem;
  width: 1rem;
  margin-left: .5rem;
}

.edit-icon svg:hover {
  opacity: .3;
  transition: .3s;
}

.rct-items .edit-icon svg {
  margin-left: 1rem;
}

.resource-group {
  position: relative;
}


.add-resource {
  position: absolute;
  bottom: -4px;
  left: 0;
  right: 0;
  font-size: 12px;
  border: 1px solid #000;
  border-radius: 100%;
  padding: 1px;
  margin: 0 auto;
  cursor: pointer;
  z-index: 1000;
  transition-duration: .15s;
}

.add-resource:hover {
  transform: scale(1.25);
}

.react-calendar-timeline .rct-sidebar .rct-sidebar-row {
  overflow: visible;
  overflow-x: clip;
}

.react-calendar-timeline .rct-sidebar {
  overflow: visible;
}

.action-icons {
  display: inline-flex;
  align-items: center;
  gap: 2px;
  width: 100%;
  padding: 0 5px;
}

.action-icons .remove-icon {
  transform: rotate(45deg);
}

.action-icons .reorder-icons {
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  gap: 2px;
}

.order-icon.disabled svg {
  fill: #c3c3c3;
}

.action-icon {
  opacity: 1;
  transition-duration: .5s;
}

.action-icon:hover {
  opacity: .5;
}

.modal-switch {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr;
}

.modal-switch-button {
  padding: 5px 10px;
  background-color: #E3E3E3;
  color: #333;
  transition-duration: .5s;
  text-align: center;
  border: 1px solid #333;
  cursor: pointer;
  opacity: .7;
}

.modal-switch-button:hover {
  opacity: 1;
}

.modal-switch-button:first-of-type {
  border-radius: 5px 0 0 5px;
}

.modal-switch-button:last-of-type {
  border-radius: 0 5px 5px 0;
}

.modal-switch-button.active {
  background-color: #2196f3;
  color: #fff;
  opacity: 1;
}